                    <li>✅ Го намалуваме загадувањето на водата и почвата</li>
                 </ul>
                 <p>
Секоја пластика има свој код кој означува колку е лесно или тешко да се рециклира. Со следење на овие симболи и со правилно одложување на отпадот, можеме да придонесеме кон одржлива иднина за сите нас.
<br>
Категории на пластика и нивната способност за рециклирање:
<ul class="symbolfoto">
    <li class="easy"> PET (1) – Шишиња за вода, пакувања за зачини → ✅ Лесни за рециклирање</li>
