    <li class="moderate">LDPE (4) – Кеси за намирници и обвивки → 🟠 Може да се рециклираат</li>
    <li class="moderate">PP (5) – Контејнери за јогурт и павлака → 🟠 Може да се рециклираат</li>
    <li class="difficult">PS (6) – Кутии за храна за носење, чашки за кафе → ❌ Тешки за рециклирање</li>
